
import { defineComponent, ref } from 'vue'
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import TextEditor from '@/shared/components/TextEditor/TextEditor.vue'
import SelectOption from '@/shared/components/SelectOption/SelectOption.vue';
import baseTab from "@/shared/components/Tab/BaseTab.vue";
import basePan from "@/shared/components/Tab/BaseTabPan.vue";
import FormatInput from "@/shared/components/FormatInput/FormatInput.vue";
import { screenMode } from '@/shared/type/Approval.type';
import { APM20200113_RES } from '@/shared/type/APM20200113.type';
import { modalController } from "@ionic/vue";
// import { COM21700112_RES, COM21700112_REQ, List } from '@/shared/type/COM216004SC01.type';

export default defineComponent({
    name: "com216004sc01",
    components: {
        BreadCrumb,
        TextEditor,
        SelectOption,
        FormatInput,
        baseTab,
        basePan
    },

    data() {

        return {
            headersIcon: [
                { key: "En", name: "common.english" },
                { key: "Km", name: "common.khmer" },
                { key: "Ko", name: "common.korean" },
            ],

            notificationLangDefaultValue: ref([
                { languageCode: "common.english", messageTitle: "", messageDescription: "", messageDescriptionHTML: "", messageID: "", statusCode: "" },
                { languageCode: "common.khmer", messageTitle: "", messageDescription: "", messageDescriptionHTML: "", messageID: "", statusCode: "" },
                { languageCode: "common.korean", messageTitle: "", messageDescription: "", messageDescriptionHTML: "", messageID: "", statusCode: "" },
            ]),

            notification: ref({
                messageID: '',
                messageTypeCode: '',
                eventDescription: '',
                pushCategory: '',
                pushIcon: '',
                SMSEventTypeCode: '',
                SMSEventDetailCode: '',
                referenceServiceID: '',
                deepLinkScreenID: '',
                statusCode: '',
            }),

            isEdit: true as boolean,
            isInValidInput: false as boolean,
            operationMode: 'normal' as screenMode,
            approvalRes: {} as any,
        }
    },

    computed: {
        title(): string {
            return this.$route.query.type == 'register' ? 'Notification Register' : 'Notification Edit'
        },
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },
        btnDelete() {
            if (this.$route.query.type == 'edit' && this.isEdit) {
                return true
            }
            return false
        },
        btnList() {
            if (this.$route.query.type == 'edit' && this.isEdit) {
                return true
            }
            return false
        },
        btnCancel() {
            if (!this.isEdit) {
                return true
            }
            return false
        },
        btnEdit() {
            if (this.$route.query.type == 'edit' && this.isEdit) {
                return true
            }
            return false
        },
        btnSave() {
            if ((this.$route.query.type == 'edit' && !this.isEdit) || (this.$route.query.type == 'register')) {
                return true
            }
            return false
        }
    },

    async mounted() {
        if (this.approvalType === '') {
            if (this.$route.query.type === "register") {
                this.isEdit = false
                this.$util.editableContent(true);
            } else {
                this.$util.editableContent(false);
                this.requestNewEventDetail()
            }
        } else {
            await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
                let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                this.approvalRes = authDetail.body
                this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'
                if (this.$util.checkServiceIdType(authDetail.body.serviceID) == 'delete') {
                    const reqBody = { newsEventID: Number(authDetailParsed.newsEventID) } as any
                    const res = await this.$request.getRequest("COM21700112", reqBody, false) as any
                    await this.assignValueToTab(this.convertCommonCode(res?.body.list))
                    this.notification = {
                        messageID: res.body.messageID,
                        eventDescription: res.body.eventDescription,
                        messageTypeCode: res.body.messageTypeCode,
                        pushCategory: res.body.pushCategory,
                        pushIcon: res.body.pushIcon,
                        SMSEventTypeCode: res.body.SMSEventTypeCode,
                        SMSEventDetailCode: res.body.SMSEventDetailCode,
                        statusCode: res.body.statusCode,
                        referenceServiceID: res.body.referenceServiceID,
                        deepLinkScreenID: res.body.deepLinkScreenID
                    }
                } else {
                    await this.assignValueToTab(this.convertCommonCode(authDetailParsed.list))
                    this.notification = {
                        messageID: authDetailParsed.messageID,
                        eventDescription: authDetailParsed.eventDescription,
                        messageTypeCode: authDetailParsed.messageTypeCode,
                        pushCategory: authDetailParsed.pushCategory,
                        pushIcon: authDetailParsed.pushIcon,
                        SMSEventTypeCode: authDetailParsed.SMSEventTypeCode,
                        SMSEventDetailCode: authDetailParsed.SMSEventDetailCode,
                        statusCode: authDetailParsed.statusCode,
                        referenceServiceID: authDetailParsed.referenceServiceID,
                        deepLinkScreenID: authDetailParsed.deepLinkScreenID
                    }
                }
            })
        }
    },

    watch: {
        notificationLangDefaultValue: {
            handler(value) {
                value.forEach((item) => {
                    item.messageDescription = this.$validate.formatHtmlToString(item.messageDescriptionHTML)
                })
            },
            deep: true
        }
    },

    methods: {
        async requestNewEventDetail() {
            const reqBody = { messageID: Number(this.$route.query.messageID) }
            const res = await this.$request.getRequest("COM21700112", reqBody, false) as any
            await this.assignValueToTab(this.convertCommonCode(res?.body.list))
            this.notification = {
                messageID: res.body.messageID,
                deepLinkScreenID: res.body.deepLinkScreenID,
                eventDescription: res.body.eventDescription,
                messageTypeCode: res.body.messageTypeCode,
                pushCategory: res.body.pushCategory,
                pushIcon: res.body.pushIcon,
                referenceServiceID: res.body.referenceServiceID,
                SMSEventDetailCode: res.body.SMSEventDetailCode,
                SMSEventTypeCode: res.body.SMSEventTypeCode,
                statusCode: res.body.statusCode
            }
        },

        async handleRegister() {
            const reqBody = {
                messageID: this.notification.messageID,
                messageTypeCode: this.notification.messageTypeCode,
                eventDescription: this.notification.eventDescription,
                pushCategory: this.notification.pushCategory,
                pushIcon: this.notification.pushIcon,
                SMSEventTypeCode: this.notification.SMSEventTypeCode,
                SMSEventDetailCode: this.notification.SMSEventDetailCode,
                statusCode: this.notification.statusCode,
                list: this.convertLangToCode(JSON.parse(JSON.stringify(this.notificationLangDefaultValue))),
            } as any;
            const res = await this.$request.getRequest("COM21700121", reqBody, false) as any;
            if (res.header.result) {
                this.$router.push("ICM201001SC00");
                this.$dialog.onOpen({
                    content: this.$t("message.register_success"),
                    dialogMode: 'Success',
                    callback: () => {
                        modalController.dismiss()
                    }
                });
            }
        },

        async handleEdit() {
            const reqBody = {
                messageID: this.notification.messageID,
                messageTypeCode: this.notification.messageTypeCode,
                eventDescription: this.notification.eventDescription,
                pushCategory: this.notification.pushCategory,
                pushIcon: this.notification.pushIcon,
                SMSEventTypeCode: this.notification.SMSEventTypeCode,
                SMSEventDetailCode: this.notification.SMSEventDetailCode,
                statusCode: this.notification.statusCode,
                list: this.convertLangToCode(JSON.parse(JSON.stringify(this.notificationLangDefaultValue))),
            } as any;
            const res = await this.$request.getRequest("COM21700131", reqBody, false) as any;
            if (res.header.result) {
                this.isEdit = true;
                this.$util.editableContent(false)
                this.$dialog.onOpen({
                    content: this.$t("message.update_success"),
                    dialogMode: 'Success',
                    callback: () => {
                        this.requestNewEventDetail()
                        modalController.dismiss()
                    }
                });
            }
        },

        convertCommonCode(listArr: any[]): any[] {
            const array = listArr.map((item: any) => {
                item.languageCode = item.languageCode ? this.$codeConverter.convertValue("LanguageCodeTranslate", item.languageCode) : ''
                return item
            });
            return array
        },

        convertLangToCode(listsArr) {
            const filter = listsArr.find((item) => item.languageCode === 'common.english')
            if (filter) {
                const data = listsArr.map((item) => {
                    if (!item.messageDescription && !item.messageTitle) {
                        item.messageDescription = filter.messageDescription
                        item.messageDescriptionHTML = filter.messageDescriptionHTML
                        item.messageTitle = filter.messageTitle
                        item.statusCode = filter.statusCode
                        item.messageID = filter.messageID
                    }
                    item.languageCode = this.$codeConverter.convertValue("LanguageCodeTranslate", item.languageCode, "value");
                    return item;
                });
                return data;
            }
        },

        async assignValueToTab(list) {
            for (let i = 0; i < list.length; i++) {
                this.notificationLangDefaultValue.map((item) => {
                    if (item.languageCode === list[i].languageCode) {
                        item.messageTitle = list[i].messageTitle;
                        item.messageDescription = list[i].messageDescription;
                        item.messageDescriptionHTML = list[i].messageDescriptionHTML;
                        item.statusCode = list[i].statusCode;
                        item.messageID = list[i].messageID;
                    } else if (item.messageDescriptionHTML === '') {
                        item.messageDescriptionHTML = '<p><br></p>'
                    }
                })
            }
        },

        handleClickAction(actionType: string) {
            switch (actionType) {
                case "cancel":
                    this.$dialog.onOpen({
                        content: `${this.$route.query.type === 'edit' ? this.$t("message.cancel_without_saving_changes") : this.$t('message.are_you_sure_you_want_to_cancel_registration?')}`,
                        dialogMode: "Question",
                        callback: () => {
                            if (this.$route.query.type == "edit") {
                                this.isEdit = true
                                this.$util.editableContent(false)
                                this.isInValidInput = false
                                this.requestNewEventDetail()
                            } else {
                                this.$router.push("COM216004SC00")
                            }
                            modalController.dismiss()
                        },
                    });
                    break;
                case "save":
                    var item: any = this.notificationLangDefaultValue.find((data) => data.languageCode === 'common.english');
                    if (item.messageDescription && item.messageTitle && this.notification.SMSEventDetailCode && this.notification.SMSEventTypeCode && this.notification.messageID && this.notification.statusCode && this.notification.pushCategory && this.notification.messageTypeCode) {
                        this.$dialog.onOpen({
                            content: this.$t("message.would_you_like_to_save"),
                            dialogMode: "Question",
                            callback: () => {
                                if (this.$route.query.type == "edit") {
                                    this.handleEdit()
                                } else {
                                    this.handleRegister()
                                }
                                modalController.dismiss()
                            },
                        });
                    } else {
                        this.isInValidInput = true;
                        this.$util.scrollToElement('inp_error')
                    }
                    break;
                case "edit":
                    this.$util.editableContent(true)
                    this.isEdit = false
                    break;
                default:
                    this.$router.push("COM216004SC00")
                    break;
            }
        },
    }
})
